import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link, NavLink } from "react-router-dom";
class NewsItem extends Component {

    chuyenDoiURL = (str) => {
        // Chuyn ht sang ch thng
        str = str.toLowerCase();

        // xa du
        str = str.replace(/(||||||||||||||||)/g, 'a');
        str = str.replace(/(||||||||||)/g, 'e');
        str = str.replace(/(||||)/g, 'i');
        str = str.replace(/(||||||||||||||||)/g, 'o');
        str = str.replace(/(||||||||||)/g, 'u');
        str = str.replace(/(||||)/g, 'y');
        str = str.replace(/()/g, 'd');

        // Xa k t c bit
        str = str.replace(/([^0-9a-z-\s])/g, '');

        // Xa khong trng thay bng k t -
        str = str.replace(/(\s+)/g, '-');

        // xa phn d -  u
        str = str.replace(/^-+/g, '');

        // xa phn d -  cui
        str = str.replace(/-+$/g, '');

        // return
        return str;
    }

    render() {
        var link = this.props.link;
        return (
            <div className={this.props.classname}>
                <div className={this.props.col1}>
                    <img src={this.props.images} width="100%" alt="" />
                </div>
                <div className={this.props.col2}>
                    <Link to={"/chi-tiet-tin/" + this.chuyenDoiURL( this.props.title) + ".html"}><h4 className="text-uppercase m-0">{this.props.title}</h4></Link>
                    <p>{this.props.name}</p>
                </div>
            </div>
        );
    }
}

export default NewsItem;